<template>
    <div class="feedback-details">
        <div class="public-detail">
            <div class="important-info" v-if="pathData[0]">
                <Row>
                    <Col span="8">
                        <div class="name">货运要求编号：</div>
                        <div class="content">{{pathData[0].freightReqCode}}</div>
                    </Col>
                    <Col span="8">
                        <div class="name">录入时间：</div>
                        <div class="content">{{timestampFormat(pathData[0].createDatetime)}}</div>
                    </Col>
                    <Col span="8" class="button">
                        <div class="btn" @click="recommendation" v-if="recommendationShow">查看推荐方案</div>
                    </Col>
                </Row>
            </div>
        </div>
        <!-- 基本信息 -->
        <div class="public-table-content" v-if="pathData[0]">
            <div class="l-content">
                <div class="table-data">
                    <ul>
                        <p class="table-title">基本信息：</p>
                        <li>
                            <span class="table-key">启运地：</span>
                            <span class="table-val">
                                <p class="value" v-for="(item, key) of pathData[0].custStartPlaces.split(';')" :key="key">{{item}}</p>
                            </span>
                        </li>
                        <li>
                            <span class="table-key">目的地：</span>
                            <span class="table-val">
                                <p class="value" v-for="(item, key) of pathData[0].custEndPlaces.split(';')" :key="key">{{item}}</p>
                            </span>
                        </li>
                        <li>
                            <span class="table-key">中转地：</span>
                            <span class="table-val" v-if="pathData[0].custTransitPlaces !== null">
                                <p class="value" v-for="(item, key) of pathData[0].custTransitPlaces.split(';')" :key="key">{{item}}</p>
                            </span>
                        </li>
                        <li>
                            <span class="table-key">货物类型：</span>
                            <span class="table-val">
                                <p>{{pathData[0].cargoType}}</p>
                            </span>
                        </li>
                        <li>
                            <span class="table-key">运输类型：</span>
                            <span class="table-val">
                                <p>{{pathData[0].trspType}}</p>
                            </span>
                        </li>
                        <li>
                            <span class="table-key">物流类型：</span>
                            <span class="table-val">
                                <p>{{pathData[0].logisticsType}}</p>
                            </span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="path-info">
            <Row>
                <Col span="24">
                    <div class="name">货运路线：</div>
                    <ul class="path-list">
                         <li v-for="(list, index) of pathData" :key="index"
                            @click="pathClick(index, list)" 
                            :class="{'path-click': pathNum == index}">
                            <span v-for="(item, key) of list.roadList" :key="key">
                                {{item.departurePort}}
                                <i>-</i>
                                <template v-if="key===list.roadList.length-1">
                                <span class="path last-path">{{item.destinationPort}}</span>
                            </template>
                            </span>
                        </li>
                    </ul>
                </Col>
            </Row>
        </div>
        <div class="path-info-details">
            <div class="details-switchover" v-for="(pathList, pathIndex) of pathData" :key="pathIndex" v-show="pathIndex === pathNum">
                <div class="title">
                    <adt-tab-btns :height="40" :width="200" :isUnderline="isUnderline" :titleNames="titleNames" :btnIndex="changeIndex" @handleBtn="handleBtn"/>
                </div>
                <!-- 路线参数模块 -->
                <div class="route-param" v-if="tabBarShow">
                    <p class="left-tit">
                        <span @click="leftTabClick(0)" :class="{'tab-click': tabNum == 0}">运输服务</span>
                        <span @click="leftTabClick(1)" :class="{'tab-click': tabNum == 1}">节点服务</span>
                    </p>
                    <!-- 运输服务模块 -->
                    <div class="route-param-content" v-if="transportServiceShow">
                        <ul>
                            <li class="path" v-for="(list, index) of pathData[pathIndex].roadList" :key="index"
                            @click="lineSectionsClick(index, pathIndex)" 
                            :class="{'path-click': pathData[pathIndex].lineNum == index || (!pathData[pathIndex].lineNum && index === 0)}">
                                <span>
                                    {{list.departurePort}}
                                    <i>-</i>
                                    {{list.destinationPort}}
                                </span>
                            </li>
                        </ul>
                        <Divider />
                        <div class="public-table-content">
                            <div class="l-content">
                                <!-- 运输服务 -->
                                <div class="table-data" v-for="(list, index) of pathList.roadList" :key="index" v-show="pathList.lineNum == index || (!pathList.lineNum && index === 0)">
                                    <ul>
                                        <p class="table-title">
                                            <span>服务参数：</span>
                                            <span class="look-btn" @click="lookAttachingTask(list, index)">查看附加任务</span>
                                        </p>
                                        <li>
                                            <span class="table-key">运输方式：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.trspMode}}</p>
                                            </span>
                                        </li>
                                        <li>
                                            <span class="table-key">物流类型：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.logisticsType}}</p>
                                            </span>
                                        </li>
                                        <li>
                                            <span class="table-key">运输类型：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.trspType}}</p>
                                            </span>
                                        </li>
                                        <li>
                                            <span class="table-key">集装箱来源：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.contaSource}}</p>
                                            </span>
                                        </li>
                                        <li>
                                            <span class="table-key">集装箱尺寸类型：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.contaSizeType}}</p>
                                            </span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- 节点服务模块 -->
                    <div class="route-param-content" v-else>
                        <ul>
                            <li class="path one-path" 
                                v-for="(list, index) of pathList.nodeList" :key="index" 
                                @click="nodeSectionsClick(index, pathIndex)" 
                                :class="{'path-click': pathList.nodeNum == index || (!pathList.nodeNum && index === 0)}">{{list.nodePort}}</li>
                        </ul>
                        <Divider />
                        <div class="public-table-content">
                            <div class="l-content">
                                <div class="table-data" v-for="(list, index) of pathList.nodeList" :key="index" v-show="pathList.nodeNum == index || (!pathList.nodeNum && index === 0)">
                                    <ul>
                                        <p class="table-title">
                                            <span>基本信息：</span>
                                            <span class="look-btn" @click="lookAttachingTask(list, index)">查看附加任务</span>
                                        </p>
                                        <li>
                                            <span class="table-key">物流类型：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.logisticsType}}</p>
                                            </span>
                                        </li>
                                        <li>
                                            <span class="table-key">运输类型：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.trspType}}</p>
                                            </span>
                                        </li>
                                        <li>
                                            <span class="table-key">集装箱来源：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.contaSource}}</p>
                                            </span>
                                        </li>
                                        <li>
                                            <span class="table-key">集装箱尺寸类型：</span>
                                            <span class="table-val">
                                                <p class="value">{{list.serverParam.contaSizeType}}</p>
                                            </span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- 运输要求模块 -->
                <div class="transport-requiry public-detail" v-else v-for="(list, index) of pathData" :key="index" v-show="index === pathNum">
                    <div class="normal-info-table two-cell">
                        <div class="table-box">
                            <div class="table">
                                <div class="cell">
                                    <div class="cell-name">主要运输方式：</div>
                                    <div class="cell-content">{{list.mainTrspMode}}</div>
                                </div>
                                <div class="cell">
                                    <div class="cell-name">最低时长要求：</div>
                                    <div class="cell-content">{{list.lowestTimeReq}}{{list.timeUnit}}</div>
                                </div>
                                <!-- <div class="cell">
                                    <div class="cell-name">提箱类型：</div>
                                    <div class="cell-content">{{list.suitcaseType}}</div>
                                </div> -->
                                <div class="cell">
                                    <div class="cell-name">最高价格要求：</div>
                                    <div class="cell-content">{{list.highestCostReq}}{{list.currencyUnit}}</div>
                                </div>
                                <!-- <div class="cell">
                                    <div class="cell-name">集装箱数量：</div>
                                    <div class="cell-content">{{list.contaQty}}</div>
                                </div> -->
                                <div class="cell">
                                    <div class="cell-name">货物适宜温度：</div>
                                    <div class="cell-content">{{list.cargoSuitTemp}}{{list.tempUnit}}</div>
                                </div>
                                <div class="cell">
                                    <div class="cell-name">报关地要求：</div>
                                    <div class="cell-content">{{list.customsReq}}</div>
                                </div>
                                <div class="cell">
                                    <div class="cell-name">最迟起运时间：</div>
                                    <div class="cell-content">{{timestampFormat(list.lastStartTime)}}</div>
                                </div>
                                <div class="cell">
                                    <div class="cell-name">最晚到达时间：</div>
                                    <div class="cell-content">{{timestampFormat(list.lastArriveTime)}}</div>
                                </div>
                                <div class="cell">
                                    <div class="cell-name"></div>
                                    <div class="cell-content"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <p class="remark" v-if="pathData[0]">
                <span class="remark-tit">货运要求备注：</span>
                <span class="remark-val">
                    <span>{{pathData[0].freightBrief}}</span>
                </span>
            </p>
        </div>
        <adt-my-modal title="查看附加任务" :showModal="isModalShow" @on-visible-change="visibleChange" width="670px" @on-ok="handleConfirm">
            <div class="scene-box"><span class="scene" v-for="(item, key) of taskData" :key="key" @click="attachingTaskClick(key, item)" :class="{'path-click': attachingTaskNum === key}">{{item.sceneName}}</span></div>
            <ul class="attaching-task-tit">
                <p class="attaching-task-content" v-for="(item, key) of taskData" :key="key" v-show="attachingTaskNum === key">
                    <span class="title">选择附加任务:</span>
                    <span class="task-list" v-for="(list, index) of item.affixList" :key="index">{{list.taskName}}</span>
                </p>
            </ul>
        </adt-my-modal>
    </div>
</template>

<script>
import { timestampFormat } from '@/libs/utils'
import { apiQueryDetailFreight } from '@/api/freight'
export default {
    name: 'FeedbackDetails',
    props: {
        id: ''
    },
    data() {
        return {
            // 附加任务
            taskData: [],
            isModalShow: false,
            btnIndex: 1,
            titleTabNames: ['客户录入信息', '销售编辑信息', '调度编辑信息'],
            isUnderline: true,
            changeIndex: 1,
            titleNames: ['路线参数', '运输要求'],
            // 货运路线
            pathNum: 0,
            // 左侧tab
            tabNum: 0,
            // 附加任务的场景
            attachingTaskNum: 0,
            tabBarShow: true,
            // 运输服务模块
            transportServiceShow: true,
            // 客户的反馈或者未反馈
            isClientFeedback: true,
            // 查看推荐方案按钮展示
            recommendationShow: false,
            pathData: []
        }
    },
    mounted() {
        this.getData()
    },
    methods: {
        getData() {
            const freightReqCode = this.id
            apiQueryDetailFreight({ freightReqCode }).then(res => {
                this.pathData = res.data
            })
        },
        // 格式化时间
        timestampFormat (timeStamp) {
            return timestampFormat (timeStamp)
        },
        visibleChange(changeStatus) {
            this.isModalShow = changeStatus
        },
        // 点击附加任务的场景
        attachingTaskClick(key) {
            console.log(key)
            this.attachingTaskNum = key
        },
        goToOtherProductList (index) {
            this.btnIndex = index+1
            switch (index) {
                case 0:
                    console.log('客户')
                    break
                case 1:
                    console.log('销售')
                    break
                case 2:
                    console.log('调度')
                    break
            }
        },
        // 点击货运路线
        pathClick(key, value){
            this.pathNum = key
        },
        // 点击下面的tab
        handleBtn (index, titleItem) {
            console.log(titleItem)
            this.changeIndex = index + 1
            if (titleItem == '运输要求') {
                this.tabBarShow = false
            } else {
                this.tabBarShow = true
            }
        },
        // 点击下面的路段
        lineSectionsClick(index, pathIndex) {
            this.$set(this.pathData[pathIndex], 'lineNum', index)
        },
        // 点击下面的节点
        nodeSectionsClick(index, pathIndex) {
            this.$set(this.pathData[pathIndex], 'nodeNum', index)
        },
        // 点击左侧tab
        leftTabClick(key) {
            this.tabNum = key
            if(this.tabNum == 0) {
                this.transportServiceShow = true
            } else {
                this.transportServiceShow = false
            }
        },
        // 查看推荐方案
        recommendation() {},
        // 查看附加任务
        lookAttachingTask(list, index) {
            this.isModalShow = true
            this.taskData = list.reqTaskList
            // this.taskData= [
            //     {sceneName: '场景1',
            //         affixList: [
            //             {taskName: '1', taskCode: '12345'},
            //             {taskName: 'fu22222renwu1', taskCode: '12345'},
            //         ]
            //     }
            // ]
            // console.log(this.taskData)
        },
        handleConfirm() {
            this.isModalShow = false
        }
    }
}
</script>
<style lang="scss" scoped>
.scene-box{
    margin: 0 28px;
    white-space: nowrap;
    overflow: auto;
    @extend %scrollbar-crosswise;
    .scene{
        display: inline-block;
        padding: 0px 30px;
        background: #E5E5E5;
        border-radius: 4px;
        margin-left: 20px;
        cursor: pointer;
        &:first-child{
            margin: 0;
        }
    }
}
.attaching-task-tit{
    border-top: 1px dashed #c8c8c8;
    margin-top: 10px;
}
.attaching-task-content{
    margin: 8px 30px;
    .ivu-checkbox-wrapper{
        width: 31%;
        text-align: center;
    }
}
.path-click{
    background-color:$info-prompt-color!important;
    border: 1px solid $light-color!important;
}
.attaching-task-content{
    margin: 8px 30px;
    .title{
        display: block;
        color: #333;
        margin-bottom: 14px;
    }
    ul{
        li{
            display: inline-block;
            width: 144px;
            text-align: center;
            cursor: pointer;
        }
    }
    .task-list{
        width: 25%;
        display: inline-block;
    }
}
.path-info .path-list{
    width: 90%!important;
}
.remark{
    margin: 10px 20px;
    margin-top: 0;
    .remark-tit{
        vertical-align: middle;
        font-weight: bold;
    }
    .remark-val{
        display: inline-block;
        width: 89%;
        min-height: 50px;
        vertical-align: middle;
        padding: 14px 30px;
        background: #f9f9f9;
        border: 1px solid #e5e5e5;
        margin-top: 8px;
    }
}
// .left-tit{
//     span{
//         border: 1px solid #fff;
//     }
// }
.tab-click{
    color: $light-color;
    background-color: #F9F9F9!important;
    border: 1px solid $light-color;
}
// .feedback-details .path-info-details .details-switchover .route-param .left-tit span{
//     border: none;
// }
</style>